import axios from 'axios'

export default {
  getHomeList (params = {}, url = '/home/getHomeData') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params})
  },
  methodDuration (params = {}, url = '/home/methodDuration') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params})
  },
  concurrent (params = {}, url = '/home/concurrent') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params})
  },
  getErrorList (params = {}, url = '/data/errorList') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params})
  }
}



