<template>
    <div style="background:#F2F6FC;padding:10px">
        <el-row ref="form" :model="form">
            <el-col :span="6">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;" >
                        <el-tooltip  placement="top">
                            <el-button>应用数量</el-button>
                        </el-tooltip>
                        <div @click="dialogTableVisible = true" style="weight:100%;padding:10px" >
                            <span   style="font-size:1.2cm;">
                                {{form.appcount}}
                            </span>
                        </div>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="6">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;">
                        <el-tooltip  placement="top">
                            <el-button>实例数量</el-button>
                        </el-tooltip>
                        <div style="weight:100%;padding:10px">
                            <span style="font-size:1.2cm;">
                                 {{form.instances}}
                            </span>
                        </div>
                    </el-card>
                 </div>
            </el-col>
            <el-col :span="6">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;">
                        <el-tooltip  placement="top">
                            <el-button>IP主机数</el-button>
                        </el-tooltip>
                        <div style="weight:100%;padding:10px">
                            <span style="font-size:1.2cm;">
                                 {{form.ips}}
                            </span>
                        </div>
                    </el-card>
                 </div>
            </el-col>
            <el-col :span="6">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;">
                        <el-tooltip  placement="top">
                            <el-button>接口数量</el-button>
                        </el-tooltip>
                        <div style="weight:100%;padding:10px">
                            <span style="font-size:1.2cm;">
                                 {{form.ifcount}}
                            </span>
                        </div>
                    </el-card>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <div style="background:#F2F6FC;padding:10px"  >
                    <el-card class="box-card" style="height:400px;background:#F2F6FC;padding:10px">
                        <i-echarts style="width:100%; height:400px;" :option="pipData"
                                   :loading="chartLoading"></i-echarts>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="12">
                <div style="background:#F2F6FC;padding:10px">
                    <el-card class="box-card" style="height:400px;background:#F2F6FC;">
                        <el-tabs type="border-card">
                            <el-tab-pane   label="并发排行榜">
                                <el-table :data="currList"  stripe="true" width="100%" align="right" :stripe="true"
                                          header-align="right" max-height="550">
                                    <el-table-column  prop="name" label="Service"></el-table-column>
                                    <el-table-column align="center" prop="concurrent" label="Concurrent Count"></el-table-column>
                                </el-table>
                            </el-tab-pane>
                            <el-tab-pane   label="耗时排行榜">
                                <el-table :data="topList"  stripe="true" width="100%" align="right" :stripe="true"
                                          header-align="right" max-height="550">
                                    <el-table-column  prop="name" label="Service"></el-table-column>
                                    <el-table-column align="center" prop="duration" label="Duration(ms)"></el-table-column>
                                </el-table>
                            </el-tab-pane>
                            <el-tab-pane   label="异常排行榜">
                                <el-table :data="errorList"  stripe="true" width="100%" align="right" :stripe="true"
                                          header-align="right" max-height="550">
                                    <el-table-column  prop="key" label="Service"></el-table-column>
                                    <el-table-column align="center" prop="value" label="Error Count"></el-table-column>
                                </el-table>
                            </el-tab-pane>

                        </el-tabs>
                    </el-card>
                </div>
            </el-col>
        </el-row>

        <!--<el-row>-->
            <!--<el-col :span="24">-->
                    <!--<el-card class="box-card" style="background:#F2F6FC;padding:10px">-->
                        <!--<el-date-picker-->
                                <!--v-model="date"-->
                                <!--type="date"-->
                                <!--placeholder="选择日期"-->
                                <!--format="yyyy MM dd "-->
                                <!--value-format="yyyyMMdd">-->
                        <!--</el-date-picker>-->
                        <!--<el-select v-model="type" placeholder="默认查询一小时数据"  @change="getChartData" filterable >-->
                            <!--<el-option v-for="s in typeData" :key="s.value" :value="s.value" :label="s.label"></el-option>-->
                        <!--</el-select>-->
                        <!--<el-button @click="getChartData">刷新数据</el-button>-->

                        <!--<el-radio-group v-model="seleType" @change="changeCharts(seleType)">-->
                            <!--<el-radio :label="1">吞吐量</el-radio>-->
                            <!--<el-radio :label="2">响应时间</el-radio>-->
                        <!--</el-radio-group>-->
                        <!--<i-echarts style="width:100%;height: 720px;" :option="chartData"-->
                                   <!--:loading="chartLoading"></i-echarts>-->
                    <!--</el-card>-->
            <!--</el-col>-->
        <!--</el-row>-->

    </div>

</template>

<script>
  import service from './service'
  import IEcharts from 'vue-echarts-v3/src/lite.js'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/chart/pie'
  import 'echarts/lib/component/dataZoom'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/toolbox'
  import 'echarts/lib/component/title'

  export default {
    name: 'home',
    data () {
      return {
        date: '',
        seleType: 1,
        type: '0',
        typeData: [{
          value: '0',
          label: '一小时'
        },
        {
          value: '1',
          label: '一天'
        },
        {
          value: '2',
          label: '周期'
        }],
        currTab: '1',
        currRow: {},
        homeList: [],
        topList: [],
        errorList: [],
        currList: [],
        form: {
          appcount: '',
          ips: '',
          instances: '',
          ifcount: ''
        },
        pipData: {
          title: {
            text: '服务实例流量占比',
            x: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a}  <br/> {c} ({d}%)<br/>{b} '
          },
          legend: {
            orient: 'vertical',
            show: false
          },
          series: [
            {
              name: '处理量',
              type: 'pie',
              radius: '55%',
              center: ['50%', '60%'],
              data: [],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        chartData: {
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            show: false,
            data: []
          },
          title: {
            text: 'Throughput/min'
          },
          toolbox: {
            feature: {
              dataZoom: {
                yAxisIndex: 'none'
              },
              restore: {},
              saveAsImage: {}
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: []
          },
          yAxis: {
            type: 'value'
          },
          dataZoom: [{
            type: 'slider'
          }],
          series: [
            {
              name: '',
              type: 'line',
              data: []
            }
          ]
        },
        chartLoading: false
      }
    },
    methods: {
      getHomeList () {
        return service.getHomeList().then((result) => {
          this.form.appcount = result.data.appcount
          this.form.ips = result.data.ips
          this.form.instances = result.data.instances
          this.form.ifcount = result.data.ifcount
        })
      },
      getTopData () {
        return service.methodDuration().then((res) => {
          this.topList = res.data
        })
      },
      getCurrTopData () {
        return service.concurrent().then((res) => {
          this.currList = res.data
        })
      },
      getErrorTopData () {
        return service.getErrorList().then(({data = {}}) => {
          this.errorList = Object.keys(data).reduce((res, key, idx) => {
            res.push({key: key, value: data[key], index: idx + 1})
            return res
          }, [])
        })
      },
      getPipData () {
        this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
        this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
        this.axios.get('/home/getPipData').then((res) => {
          console.log(res)
          this.pipData.series = [
            {
              name: '处理量',
              type: 'pie',
              radius: '65%',
              center: ['50%', '50%'],
              selectedMode: 'single',
              data: res.data,
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        })
      },
      rowClick (row) {
        this.currRow = row
      },
      changeCharts (value) {
        if(value == '1') {//eslint-disable-line
          this.getChartData()
        }else if(value == '2') {//eslint-disable-line
          this.getDurationChartData()
        }
      },
      getDurationChartData () {
        this.chartLoading = true
        this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
        this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
        this.axios.get('/home/getDurationChartData', { params: { type: this.type, date: this.date }}).then((res) => {

        }).finally(() => {
          this.chartLoading = false
        })
      },
      getChartData () {
        this.chartLoading = true
        this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
        this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
        this.axios.get('/home/getHomeChartData', { params: { type: this.type, date: this.date }}).then((res) => {
          this.chartData.xAxis.data = []
          const xData = res.data.map((v) => {
            return {
              name: v.name,
              type: 'line',
              data: v.yaxis
            }
          })
          const xAxis = res.data[0].xaxis
          this.chartData.legend.data = xData.map(v => v.name)
          this.chartData.xAxis.data = xAxis
          this.chartData.series = xData
        }).finally(() => {
          this.chartLoading = false
        })
      }
    },
    computed: {},
    mounted () {
      this.getHomeList()
      this.getPipData()
      this.getTopData()
      // this.getChartData()
      this.getCurrTopData()
      this.getErrorTopData()
    },
    components: {
      IEcharts
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



